import React, { useRef, useLayoutEffect, useState, useEffect, useCallback } from 'react'
import CssBaseline from '@mui/material/CssBaseline'
import Grid from '@mui/material/Grid'
import { StyledEngineProvider } from '@mui/material/styles';
import Container from '@mui/material/Container'
import Typography from '@mui/material/Typography';
import Slider from '@mui/material/Slider';
import ArrowsIcon from '@mui/icons-material/Height';
import Fab from '@mui/material/Fab'
import ky from 'ky'
import NavigationIcon from '@mui/icons-material/Navigation'
import { useSnackbar } from 'notistack'
import { PopoverPicker } from "./components/PopoverPicker";
import TextField from '@mui/material/TextField';
//import smcdsbLogo from './images/555GreenSMCDSBlogo.png'
import $ from 'jquery'

import "./css/styles.css";


export default function App() {

    const { enqueueSnackbar } = useSnackbar()

    const dataset = {}

    const getLtik = () => {
        const searchParams = new URLSearchParams(window.location.search)
        const ltik = searchParams.get('ltik')
        if (!ltik) throw new Error('Missing lti key.')
        return ltik
    }

    const errorPrompt = useCallback(async (message) => {
        enqueueSnackbar(message, { variant: 'error' })
    }, [])

    // Submits resource to deep linking endpoint
    const submit = async () => {
        try {
            dataset['buttonStyle'] = buttonStyle;
            dataset['buttonText'] = buttonText;
            dataset['linkText'] = linkText;
            const form = await ky.post('/deeplink', { credentials: 'include', json: dataset, headers: { Authorization: 'Bearer ' + getLtik() } }).text()
            //const form = await ky.post('/deeplink', { credentials: 'include', headers: { Authorization: 'Bearer ' + getLtik() } }).text()
            $('body').append(form)
        } catch (err) {
            console.log(err)
            errorPrompt('Failed creating deep link! ' + err)
        }
    }

    const targetRef = useRef();
    const [buttonStyle, setButtonStyle] = useState({
        width: 'auto',
        height: 'auto',
        fontSize: '20px',
        borderRadius: '10px',
        backgroundColor: '#388E3C',
        color: 'white',
        border: 'none',
        padding: '20px 20px',
        margin: '10px 10px',
        textAlign: 'center',
        textDecoration: 'none',
        display: 'inline-flex',
        flexDirection: 'column',
        justifyContent: 'center',
        cursor: 'pointer'
    })

    const [controlSettings, setControlSettings] = useState({
        //widthMin: 0,
        //heightMin: 0,
        fontSizeMin: '20px',
        borderRadiusMin: '10px'
    })

    const [width, setWidth] = useState();
    const [height, setHeight] = useState();
    const [fontSize, setFontSize] = useState('20px');
    const [borderRadius, setBorderRadius] = useState('10px');
    const [backgroundColor, setBackgroundColor] = useState("#388E3C");
    const [textColor, setTextColor] = useState("#ffffff");
    const [buttonText, setButtonText] = useState('Google');
    const [linkText, setLinkText] = useState('https://www.google.ca/');

    const presetColors = ["#D32F2F", "#C2185B", "#7B1FA2", "#512DA8", "#303F9F", "#1976D2", "#0288D1", "#0097A7", "#00796B", "#388E3C", "#689F38", "#AFB42B", "#FBC02D", "#FFA000", "#F57C00", "#E64A19", "#5D4037", "#455A64", "#FFFFFF", "#878787", "#00000"];

    useLayoutEffect(() => {
        if (targetRef.current) {
            setControlSettings({
                ...controlSettings,
                widthMin: Math.ceil(targetRef.current.offsetWidth / 5) * 5,
                heightMin: Math.ceil(targetRef.current.offsetHeight / 5) * 5,
                fontSizeMin: targetRef.current.style.fontSize,
                borderRadiusMin: targetRef.current.style.borderRadius
            });

            setButtonStyle({
                ...buttonStyle,
                width: controlSettings['widthMin'],
                height: controlSettings['heightMin'],
                fontSize: controlSettings['fontSizeMin'],
                borderRadius: controlSettings['borderRadiusMin'],
                backgroundColor: backgroundColor,
                textColor: textColor
            });

            setWidth(controlSettings['widthMin']);
            setHeight(controlSettings['heightMin']);
            setFontSize(controlSettings['fontSizeMin']);
            setBorderRadius(controlSettings['borderRadiusMin']);
        }
    }, []);

    const handleSliderChange = (event, newValue) => {
        //console.log(event.target.name, newValue)
        //console.log("controlSettings: " + JSON.stringify(controlSettings));
        if (event.target.name === "widthSlider") {
            setWidth(newValue);
            setButtonStyle({ ...buttonStyle, width: Number(event.target.value) + 'px' });
        }
        else if (event.target.name === "heightSlider") {
            setHeight(newValue);
            setButtonStyle({ ...buttonStyle, height: Number(event.target.value) + 'px' });
        }
        else if (event.target.name === "fontSizeSlider") {
            setFontSize(newValue);
            setButtonStyle({ ...buttonStyle, fontSize: event.target.value + 'px'  });
        }
        else if (event.target.name === "radiusSlider") {
            setBorderRadius(newValue);
            setButtonStyle({ ...buttonStyle, borderRadius: event.target.value + 'px'  });
        }
    };

    const handleBackgroundColorChange = (color) => {
        //console.log(color)
        setBackgroundColor(color);
        setButtonStyle({ ...buttonStyle, backgroundColor: String(color) });
    };

    const handleTextColorChange = (color) => {
        //console.log(color)
        setTextColor(color);
        setButtonStyle({ ...buttonStyle, color: String(color) });
    };

    const handleTextChange = (event) => {
        //console.log(event);
        if (event.target.name === "buttonText") {
            setButtonText(event.target.value);
        }
        else if (event.target.name === "linkText") {
            setLinkText(event.target.value);
        }
    };

    return (
        <StyledEngineProvider injectFirst>
            <Container component='main' maxWidth='md'>
                <CssBaseline />
                <div className="paper">
                    <Grid container>
                        {/*<Grid container>*/}
                        {/*    <Grid item xs={2} className="logoParent">*/}
                        {/*        <img src={smcdsbLogo} className="logo" alt="Logo" />*/}
                        {/*    </Grid>*/}
                        {/*    <Grid item xs={10} >*/}
                        {/*        <Typography className="title" variant="h1">*/}
                        {/*            Custom Button Creator*/}
                        {/*        </Typography>*/}
                        {/*    </Grid>*/}
                        {/*</Grid>*/}
                        <Grid container className="row">
                            <Grid item xs={3} className="controls">
                                <Typography id="background-color" gutterBottom className="inputLabels" variant="h2">
                                    Background Colour
                                </Typography>
                                <PopoverPicker
                                    color={backgroundColor}
                                    onChange={handleBackgroundColorChange}
                                    presetColors={presetColors}
                                />
                            </Grid>
                            <Grid item xs={3} className="controls">
                                <Typography id="text-color" gutterBottom className="inputLabels" variant="h2">
                                    Text Colour
                                </Typography>
                                <PopoverPicker
                                    color={textColor}
                                    onChange={handleTextColorChange}
                                    presetColors={presetColors}
                                />
                            </Grid>
                            <Grid item xs={3} className="controls">
                                <Typography id="button-text" gutterBottom className="inputLabels" variant="h2">
                                    Button Text
                                </Typography>
                                <TextField
                                    name="buttonText"
                                    id="button_text"
                                    label="Button Text"
                                    size="small"
                                    value={buttonText}
                                    onChange={handleTextChange}
                                />
                            </Grid>
                            <Grid item xs={3} className="controls">
                                <Typography id="link-text" gutterBottom className="inputLabels" variant="h2">
                                    Link URL
                                </Typography>
                                <TextField
                                    name="linkText"
                                    id="link_text"
                                    label="Link URL"
                                    size="small"
                                    value={linkText}
                                    onChange={handleTextChange}
                                />
                            </Grid>
                        </Grid>
                        <Grid container className="row">
                            <Grid item xs={3} className="controls">
                                <Typography id="width-slider" gutterBottom className="inputLabels" variant="h2">
                                    Width
                                </Typography>
                                <Grid container spacing={2} alignItems="center">
                                    <Grid item>
                                        <ArrowsIcon className="widthSlider" />
                                    </Grid>
                                    <Grid item xs>
                                        <Slider
                                            name='widthSlider'
                                            value={typeof width === 'number' ? width : 0}
                                            min={typeof controlSettings['widthMin'] === 'number' ? controlSettings['widthMin'] : 0}
                                            marks={[
                                                {
                                                    value: typeof controlSettings['widthMin'] === 'number' ? controlSettings['widthMin'] : 0,
                                                    label: String(typeof controlSettings['widthMin'] === 'number' ? controlSettings['widthMin'] : 0) + 'px',
                                                },
                                                {
                                                    value: 400,
                                                    label: '400px',
                                                },
                                            ]}
                                            defaultValue={typeof controlSettings['widthMin'] === 'number' ? controlSettings['widthMin'] : 0}
                                            valueLabelDisplay="on"
                                            step={5}
                                            max={400}
                                            size="small"
                                            onChange={handleSliderChange}
                                            aria-labelledby="width-slider"
                                        />
                                    </Grid>
                                </Grid>
                            </Grid>
                            <Grid item xs={3} className="controls">
                                <Typography id="height-slider" gutterBottom className="inputLabels" variant="h2">
                                    Height
                                </Typography>
                                <Grid container spacing={2} alignItems="center">
                                    <Grid item>
                                        <ArrowsIcon />
                                    </Grid>
                                    <Grid item xs>
                                        <Slider
                                            name='heightSlider'
                                            value={typeof height === 'number' ? height : 0}
                                            min={typeof controlSettings['heightMin'] === 'number' ? controlSettings['heightMin'] : 0}
                                            marks={[
                                                {
                                                    value: typeof controlSettings['heightMin'] === 'number' ? controlSettings['heightMin'] : 0,
                                                    label: String(typeof controlSettings['heightMin'] === 'number' ? controlSettings['heightMin'] : 0) + 'px',
                                                },
                                                {
                                                    value: 200,
                                                    label: '200px',
                                                },
                                            ]}
                                            defaultValue={typeof controlSettings['heightMin'] === 'number' ? controlSettings['heightMin'] : 0}
                                            valueLabelDisplay="on"
                                            step={5}
                                            max={200}
                                            size="small"
                                            onChange={handleSliderChange}
                                            aria-labelledby="height-slider"
                                        />
                                    </Grid>
                                </Grid>
                            </Grid>
                            <Grid item xs={3} className="controls">
                                <Typography id="font-slider" gutterBottom className="inputLabels" variant="h2">
                                    Font Size
                                </Typography>
                                <Grid container spacing={2} alignItems="center">
                                    <Grid item>
                                        <ArrowsIcon />
                                    </Grid>
                                    <Grid item xs>
                                        <Slider
                                            name='fontSizeSlider'
                                            value={typeof parseInt(fontSize) === 'number' ? parseInt(fontSize) : 1}
                                            min={typeof parseInt(controlSettings['fontSizeMin']) === 'number' ? parseInt(controlSettings['fontSizeMin']) : 1}
                                            marks={[
                                                {
                                                    value: typeof parseInt(controlSettings['fontSizeMin']) === 'number' ? parseInt(controlSettings['fontSizeMin']) : 1,
                                                    label: String(typeof controlSettings['fontSizeMin'] === 'string' ? controlSettings['fontSizeMin'] : 1) + 'px',
                                                },
                                                {
                                                    value: 200,
                                                    label: '200px',
                                                },
                                            ]}
                                            defaultValue={typeof parseInt(controlSettings['fontSizeMin']) === 'number' ? parseInt(controlSettings['fontSizeMin']) : 1}
                                            valueLabelDisplay="on"
                                            step={1}
                                            max={200}
                                            size="small"
                                            onChange={handleSliderChange}
                                            aria-labelledby="font-slider"
                                        />
                                    </Grid>
                                </Grid>
                            </Grid>
                            <Grid item xs={3} className="controls">
                                <Typography id="radius-slider" gutterBottom className="inputLabels" variant="h2">
                                    Corner Radius
                                </Typography>
                                <Grid container spacing={2} alignItems="center">
                                    <Grid item>
                                        <ArrowsIcon className="radiusSlider" />
                                    </Grid>
                                    <Grid item xs>
                                        <Slider
                                            name='radiusSlider'
                                            value={typeof parseInt(borderRadius) === 'number' ? parseInt(borderRadius) : 0}
                                            min={typeof parseInt(controlSettings['borderRadiusMin']) === 'number' ? parseInt(controlSettings['borderRadiusMin']) : 0}
                                            marks={[
                                                {
                                                    value: typeof parseInt(controlSettings['borderRadiusMin']) === 'number' ? parseInt(controlSettings['borderRadiusMin']) : 0,
                                                    label: String(typeof controlSettings['borderRadiusMin'] === 'number' ? controlSettings['borderRadiusMin'] : 0) + 'px',
                                                },
                                                {
                                                    value: 100,
                                                    label: '100px',
                                                },
                                            ]}
                                            defaultValue={typeof parseInt(controlSettings['borderRadiusMin']) === 'number' ? parseInt(controlSettings['borderRadiusMin']) : 10}
                                            valueLabelDisplay="on"
                                            step={5}
                                            max={100}
                                            size="small"
                                            onChange={handleSliderChange}
                                            aria-labelledby="radius-slider"
                                        />
                                    </Grid>
                                </Grid>
                            </Grid>
                        </Grid>
                        <Grid container xs={12} >
                            <Grid item xs={9}>
                                <Grid item xs className="previewDiv">
                                    <Typography id="preview-text" gutterBottom className="previewText" variant="h2">
                                        Preview (click to test)
                                    </Typography>
                                    <a ref={targetRef} href={linkText} target="_blank" style={buttonStyle} rel="noreferrer">{buttonText}</a>
                                </Grid>
                            </Grid>
                             <Grid item xs={3} >
                                <Grid item xs className="btnDiv">
                                    <Fab variant='extended' color='primary' aria-label='add' className="fab" onClick={submit}>
                                        <NavigationIcon className="extendedIcon" />
                                        Insert
                                    </Fab>
                                </Grid>
                            </Grid>
                       </Grid>
                    </Grid>
                </div>
            </Container>
        </StyledEngineProvider>
    );
}
